  [data-aos="left-menu-1"] {
    opacity: 0;
    transform:translateX(-100%);
    transition-property: opacity , transform;
  }
  [data-aos="left-menu-1"].aos-animate {
    opacity: 1;
    transform:translateX(5vw);
   }
  [data-aos="left-menu-2"] {
    opacity: 0;
